import React from 'react';
import JobList from './JobList';
import JobRuns from './JobRuns';
import JobRunsByName from './JobRunsByName';

import Metrics from './Metrics';
import QueueStats from './QueueStats';

import jwt_decode from "jwt-decode";
import LoaderComponent from './LoaderComponent';
import CurrentTime from './CurrentTime';

import { withRouter } from 'react-router'

import '../css/App.css'
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";


class Routes extends React.Component {

  constructor(props){
    super(props); 
    this.state  = {
      jobs: [],
      metrics: [],
      user: '',
      loader:false

    }
  }

  async fetchEtlFlowJobs() {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    this.setState(prevState => ({
      loader: true
    }))
    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: `
          query {
              jobs {
                name 
                schedule
                props {key value} 
                nextSchedule 
                schduleRemainingTime 
                is_active 
                max_active_runs
             }
           }
          `
        }
      )  
    })

    if (response.status === 403) {
      this.remove_token()
    } 

    const {data} = await response.json();
    this.setState(prevState => ({
      loader: false
    }))

    return data;    
  }

  setInterval() {
    this.timerID = setInterval(
      () => this.fetchEtlFlowJobs(),
      1000 * 1000
    );
  }

  fetchEtlFlowUserDetails() {
    const token = localStorage.getItem('Authorization')
    var decoded = JSON.parse(JSON.stringify(jwt_decode(token)));
    this.setState({
      user: decoded["user"]
    })
  }

  componentDidMount() {

    //calling jobs api
    this.fetchEtlFlowJobs().then(({jobs}) => {
      this.setState(prevState => ({
        jobs: jobs
      }))
    })

    this.fetchEtlFlowUserDetails()
 }

 get_etl_jobs = () => {
  this.fetchEtlFlowJobs().then(({jobs}) => {
    this.setState(prevState => ({
      jobs: jobs
    }))
  })  
 }

 remove_token() {
  localStorage.removeItem("Authorization")
  this.props.doLogout("")
}

  render(){
    if (this.state.loader){
      return (
        <div>
          <LoaderComponent job_run_id={this.job_run_id}  set_job_run_state = {this.set_job_run_state} />
        </div>
      );
     }
    else{
    return (
      <Router>
        <nav className = "navbar navbar-expand-lg navbar-dark bg-dark mb-3">
            <ul className = "nav navbar-nav w-100">
                <li className = "nav-item nav-link" ><Link to="/">Jobs</Link> </li>
                <li className = "nav-item nav-link" ><Link to="/JobRuns">Job Runs</Link> </li>
                <li className = "nav-item nav-link" ><Link to="/getInfo">Metrics</Link> </li>
                <li className = "nav-item nav-link" ><Link to="/getQueueStats">Job Logs</Link> </li>
            </ul>
            <div class="navbar-collapse collapse w-100 order-3 dual-collapse2">
              <ul class="nav justify-content-end w-100">
              <CurrentTime/>
                <li class="nav-item dropdown">
	                <a class="nav-link  dropdown-toggle" href="#" data-toggle="dropdown">{this.state.user}</a>
	                  <ul class="dropdown-menu">
		                  <li>
                        <a class="dropdown-item" href="https://github.com/tharwaninitin/etlflow">
                           Git Hub 
                        </a>
                      </li>
                      <li>
                        <a class="dropdown-item" href="https://tharwaninitin.github.io/etlflow/site/">
                          Docs
                        </a>
                      </li>
                      <li>
                        <a class="dropdown-item">
                          <input type="button" class="btn btn-secondary btn-sm" value="LogOut" onClick = {() => this.remove_token()}></input>
                        </a>
                      </li>
	                  </ul>
	              </li>
              </ul>
            </div>
        </nav>
        <Switch>
          <Route exact path="/" component={() => (<JobList jobs={this.state.jobs} get_etl_jobs = {this.get_etl_jobs} />)}/>
          <Route path="/JobRuns/" component={() => (<JobRuns jobs={this.state.jobs}/>)}/>
          <Route path="/getInfo" component={() => (<Metrics/>)}/>
          <Route path="/getQueueStats" component={() => (<QueueStats/>)}/>
          <Route path="/JobRunsByName/:id" component={() => (<JobRunsByName/>)}/>

        </Switch>
      </Router>
    );
    }
  }
}
export default Routes;
// export default withRouter(Routes)


