import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';


//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery';
import LoaderComponent from './LoaderComponent';

class Metrics extends React.Component {
  constructor(props){
    super(props); 
    this.state  = { metrics: [],
      loader:false
    }
  }

  async fetchEtlFlowMetrics() {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    this.setState(prevState => ({
      loader: true
    }))

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: `
          query {
            metrics {
                active_jobs
                active_subscribers
                etl_jobs
                cron_jobs
                used_memory
                free_memory
                total_memory
                max_memory
                build_time
              }
           }
          `
        }
      )  
    })

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 
    
    const {data} = await response.json();
    this.setState(prevState => ({
      loader: false
    }))

    return data;    
  }

  componentDidMount() {
    //calling getInfo api
    this.fetchEtlFlowMetrics().then(({metrics}) => {
        this.setState(prevState => ({
            metrics: metrics
        }))
      })
   }


  render(){
    if (this.state.loader){
      return (
        <div>
          <LoaderComponent job_run_id={this.job_run_id}  set_job_run_state = {this.set_job_run_state} />
        </div>
      );
     }
    else{
    return (
      <div class="container">
          <ul class = "pagination justify-content-center">
             <button class= "btn btn btn-sm" type="button " onClick = {() => {
                   this.fetchEtlFlowMetrics().then(({metrics}) => {
                    this.setState(prevState => ({
                        metrics: metrics
                    }))
                  })
                }} >
               Refresh
             </button>
         </ul>
         <ul class="list-group ">
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Active Jobs</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.active_jobs}</span>
            </li>
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Active Subscribers</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.active_subscribers}</span>
            </li>
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Etl Jobs</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.etl_jobs}</span>
            </li>
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Cron Jobs</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.cron_jobs}</span>
            </li>  
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Used Memory</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.used_memory}</span>
            </li> 
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Free Memory</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.free_memory}</span>
            </li>  
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Total Memory</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.total_memory}</span>
            </li>
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Max Memory</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.max_memory}</span>
            </li> 
            <li class="list-group-item d-flex justify-content-between align-items-center">
                <b>Build Time</b>
              <span class="badge badge-secondary badge-pill">{this.state.metrics.build_time}</span>
            </li>   
         </ul>
      </div>     
    );    
   }      
  }
}
export default Metrics;
