import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';

//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery';
import LoaderComponent from './LoaderComponent';

class StepRuns extends React.Component {
  constructor(props){
    super(props); 
    this.state  = { 
      stepruns: [],
      loader:false

    }
    
  }

  async fetchEtlFlowJobById(job_run_id) {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    this.setState(prevState => ({
      loader: true
    }))


    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token
      },
      body:  JSON.stringify(
        {
          query: `
          query {
            stepruns(job_run_id:    \"${job_run_id}\"){
                job_run_id
                step_name
                properties
                state
                start_time
                elapsed_time
                step_type
                step_run_id
             }
           }
          `
        }
      )  
    })

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 

    const {data} = await response.json();
    this.setState(prevState => ({
      loader: false
    }))

    return data;    
  }

  setInterval() {
    this.timerID = setInterval(
      () => this.fetchEtlFlowJobs(),
      1000 * 1000
    );
  }
 
  componentDidMount() {
    this.fetchEtlFlowJobById(this.props.job_run_id).then(({stepruns}) => {
      this.setState(prevState => ({
        stepruns: stepruns
      }))
    })
    // set Interval
    this.setInterval();
 }

 componentWillUnmount() {
  clearInterval(this.timerID);
 }
 
 show_modal(data)
 {
   var props = ''
   var data_jobruns = JSON.parse(data)

   for(var key in data_jobruns) {
   { 
       props  += "<b>"+ key + "</b>" +"=" + data_jobruns[key]+ "<br></br>"
   }
  {$("#stepRunModal .modal-body").html(props)}
  $("#stepRunModal").modal();
 }
}

show_step_run_state_modal(data)
 {
  {$("#stepRunStateModal .modal-body").html(data)}
  $("#stepRunStateModal").modal();
 }



 render(){
   var step_run_id_flag = false
   this.state.stepruns.map( item => {if(item.step_run_id !== "") step_run_id_flag = true})
   var linked_jobs = ''
   if(step_run_id_flag) {
    linked_jobs = <th>Linked Job</th>
   } else {
    linked_jobs = ""
   }

   var remoteStep = ["EtlFlowJobStep","DPSparkJobStep","ParallelETLStep"];
   if (this.state.loader){
    return (
      <div>
        <LoaderComponent job_run_id={this.job_run_id}  set_job_run_state = {this.set_job_run_state} />
      </div>
    );
   }
   else{
   return (
     <div className="container-fluid">
         <table id="job runs" class="table table-sm table-bordered">
           <thead class="thead-dark">
                 <tr>
                   <th>Job Run Id</th>
                   <th>Step Type</th>
                   <th>Step Name</th>
                   <th>Start Time</th>
                   <th>Elapsed Time</th>
                   <th>Status</th>
                   <th>Properties</th> 
                   {linked_jobs}
                   <th>
                        <button className="btn btn-secondary btn-block" onClick = {() => this.props.set_job_run_state()}>Back</button>
                   </th>
                 </tr>
               </thead>
                 <tbody>
                 {this.state.stepruns.map( data => (
                   <tr>
                     <td>{data.job_run_id}</td>
                     <td>{data.step_type}</td>
                     <td>{data.step_name}</td>
                     <td>{data.start_time}</td>
                     <td>{data.elapsed_time}</td>
                     <td>
                     {(() => {
                           if (data.state === "pass" || data.state == "" ) {
                             return (
                               <div>
                                 <p className = "text-success">{data.state}</p>
                               </div>
                             )
                           } else if (data.state === "started" || data.state == "running" ) {
                             return (
                               <div>
                                 <p className = "text-warning">{data.state}</p>
                               </div>
                             )
                           } else {
                            return (
                              <div>
                                 <button type="button" class="btn btn-danger btn btn-sm" data-toggle="modal" onClick = {() => this.show_step_run_state_modal(data.state)}>
                                     Error
                                 </button>
                            
                                 <div class="modal fade" id="stepRunStateModal" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                                   <div class="modal-dialog" role="document">
                                     <div class="modal-content">
                                       <div class="modal-header">
                                         <h5 class="modal-title" id="exampleModalLabel">Properties </h5>
                                         <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                           <span aria-hidden="true">&times;</span>
                                         </button>
                                       </div>
                                       <div class="modal-body">
                                       </div>
                                       <div class="modal-footer">
                                         <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                       </div>
                                     </div>
                                   </div>
                                 </div>
                              </div>
                            )
                           }
                         })()}
                     </td>
                     <td>
                          <button type="button" class="btn btn-secondary btn-sm" data-toggle="modal" onClick = {() => this.show_modal(data.properties)}>
                            Properties
                          </button>
                          
                          <div class="modal fade" id="stepRunModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                            <div class="modal-dialog" role="document">
                              <div class="modal-content">
                                <div class="modal-header">
                                  <h5 class="modal-title" id="exampleModalLabel">Step Properties</h5>
                                  <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                  </button>
                                </div>
                                <div class="modal-body">
                                </div>
                                <div class="modal-footer">
                                  <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                </div>
                              </div>
                            </div>
                          </div>
                      </td>
                     <td>
                         {(() => {
                            if (remoteStep.includes(data.step_type)) {
                              return (
                                <input type="button" value="Check Progress" class="btn btn-secondary btn-sm" onClick = {() => this.fetchEtlFlowJobById(data.step_run_id).then(({stepruns}) => {
                                    this.setState(prevState => ({
                                      stepruns: stepruns
                                    }))
                                  })}/>
                              )
                            } else {
                              return (
                                <div></div>
                              )
                            }
                          })()}
                    </td>
                   </tr>
                 ))}
                 </tbody>
            </table>
          </div>
    );
    }
  }
}
export default StepRuns;     