import React from 'react';
//Bootstrap and jQuery libraries
import 'jquery/dist/jquery.min.js';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'bootstrap/dist/js/bootstrap.min.js';

import 'popper.js/dist/umd/popper.min.js';
import 'popper.js/dist/umd/popper.js';
  
import { withRouter } from 'react-router'
//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import $ from 'jquery';
import LoaderComponent from './LoaderComponent';

class QueueStats extends React.Component {
  constructor(props){
    super(props); 
    this.state  = { queueStats: [],
      loader:false
    }
  }

  async fetchEtlFlowQueueStats() {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    this.setState(prevState => ({
      loader: true
    }))


    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: `
          query {
            queueStats {
              name 
              details
              submitted_from
              execution_time
              }
           }
          `
        }
      )  
    })

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 
    
    const {data} = await response.json();
    this.setState(prevState => ({
      loader: false
    }))

    return data;    
  }

  componentDidMount() {
    //calling getInfo api
    this.fetchEtlFlowQueueStats().then(({queueStats}) => {
        this.setState(prevState => ({
          queueStats: queueStats
        }))
      })
   }

   show_modal(data)
  {
    var props = ''
    var queue = JSON.parse(data)
 
    for(var key in queue) {
    { 
        props  += "<b>"+ key + "</b>" +"=" + queue[key]+ "<br></br>"
    }

   {$("#queueStats .modal-body").html(props)}
   $("#queueStats").modal();
  }
}


  render(){
    if (this.state.loader){
      return (
        <div>
          <LoaderComponent job_run_id={this.job_run_id}  set_job_run_state = {this.set_job_run_state} />
        </div>
      );
     }
    else{ 
    return (
      <div class="container">
        <ul class = "pagination justify-content-center">
             <button class= "btn btn btn-sm" type="button " onClick = {() => {
                    this.fetchEtlFlowQueueStats().then(({queueStats}) => {
                      this.setState(prevState => ({
                        queueStats: queueStats
                      }))
                  })
                }} >
               Refresh
             </button>
         </ul>
          <table id="joblist" class="table table-sm table-bordered">
            <thead class="thead-dark">
                  <tr>
                    <th>Job Name</th>
                    <th>Submitted From</th>
                    <th>Execution Time</th>
                    <th>Details</th>
                  </tr>
                </thead>
                  <tbody>
                  {this.state.queueStats.map( (data) => (
                    <tr>
                      <td>{data.name  }</td>
                      <td>{data.submitted_from}</td>
                      <td>{data.execution_time}</td>

                      <td>
                          <button type="button" class="btn btn-secondary btn-sm" data-toggle="modal" onClick = {() => this.show_modal(data.details)}>
                            Stats
                          </button>
                          
                          <div class="modal fade" id="queueStats" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                            <div class="modal-dialog" role="document">
                              <div class="modal-content">
                                <div class="modal-header">
                                  <h5 class="modal-title" id="exampleModalLabel">Properties </h5>
                                  <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true">&times;</span>
                                  </button>
                                </div>
                                <div class="modal-body">
                                </div>
                                <div class="modal-footer">
                                  <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                </div>
                              </div>
                            </div>
                          </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
            </table>
      </div>     
    ); 
    }         
  }
}
export default withRouter(QueueStats)
// export default QueueStats;
