import React from 'react';
//Bootstrap and jQuery libraries
import 'bootstrap/dist/css/bootstrap.min.css';
import 'jquery/dist/jquery.min.js';
import jwt_decode from "jwt-decode";


class Login extends React.Component {
  constructor(props){
    super(props); 
    this.state  = {
         user_name: '',
         password: '',
         token: '', 
         login: [],

        }

    this.login_validation = this.login_validation.bind(this);
    this.set_login_user_name = this.set_login_user_name.bind(this);
    this.set_login_password = this.set_login_password.bind(this);
  }

  async fetchLoginDetails(user_name,password) {
    const GRAPHQL_URL = '/api/login'
    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
      },
      body:  JSON.stringify(
        {
          query: `
          mutation {
            login (user_name: \"${user_name}\", password: \"${password}\") {
                message 
                token
             }
           }
          `
         }
      )  
    })  
    const {data} = await response.json();
    return data;        
  }

 login_validation() {
    this.fetchLoginDetails(this.state.user_name,this.state.password).then(({login}) => {
        var token_value = JSON.parse(JSON.stringify(login)).token
        if(token_value !== "")
        {
            localStorage.setItem('Authorization', token_value);   
            this.props.doLogin(token_value)
            this.setState(prevState => ({
                token: token_value
              }))
        }
        else 
        {
            alert("Invalid User")
        }
    })   
  } 

  set_login_user_name() {
    this.setState(prevState => ({
        user_name: document.getElementById("user_name").value
      }))
  }

  set_login_password() {
    this.setState(prevState => ({
        password: document.getElementById("password").value
      }))
  }

  render(){
    return (
    <div className="main" align="center">
         <nav className = "navbar navbar-expand-lg navbar-dark bg-dark mb-3">
             <ul className = "nav navbar-nav w-50">
                 <li className = "nav-item nav-link">EtlFlow</li>
             </ul>
         </nav>   
           <div className="col-md-3">
            <div className="login-form ">
                <form id="form_login" className = "text-center border border-dark p-5" onSubmit={(e) => {this.login_validation(); e.preventDefault();}  }>
                    <p className = "h4 mb-4">EtlFlow Login</p>
                    <div className="form-group">
                        <input type="text"
                         className="form-control" 
                         aria-describedby="UsernameHelp"
                         placeholder="Username"
                         id="user_name"
                         onChange={e => this.set_login_user_name()}></input>
                    </div>
                    <div className="form-group">
                        <input
                         type="password"
                         className="form-control"
                         aria-describedby="PasswordHelp"
                         placeholder="Password"
                         id="password"
                         onChange={e => this.set_login_password()}></input>
                    </div>
                    <button type="submit" className="btn btn-secondary btn-block">Submit</button>
                </form>   
            </div>
        </div>
    </div>
    );
  }
}
export default Login;