import React from 'react';
//Bootstrap and jQuery libraries
import 'bootstrap/dist/css/bootstrap.min.css';
import 'jquery/dist/jquery.min.js';
//Datatable Modules
import "datatables.net-dt/js/dataTables.dataTables"
import "datatables.net-dt/css/jquery.dataTables.min.css"
import StepRuns from './StepRuns';
import $ from 'jquery';
import LoaderComponent from './LoaderComponent';
import { withRouter } from 'react-router'

class JobRunsByName extends React.Component {
  constructor(props){
    super(props); 
    this.job_run_id = ''
    this.filterOperation = ["IN","NOT IN"]
    this.state  = { 
      jobruns: [],
      filter_start_date: '',
      filter_end_date: '',
      paginationValue: 0,
      stepRun: false,
      loader:false
    }
    this.set_job_run_state = this.set_job_run_state.bind(this);    
    this.restartState = this.restartState.bind(this);    

  }


  restartState(event) {
    this.setState({ 
      filter_start_date: '',
      filter_end_date: '',
      paginationValue: 0,
      stepRun: false,
      loader:false
     });
  }

  async fetchEtlFlowJobs() {
    const GRAPHQL_URL = '/api/etlflow'
    const token = localStorage.getItem('Authorization')

    const limit = 10;
    const offset = this.state.paginationValue;

    
    var query = ` query { jobruns(limit: ${limit} , offset: ${offset} ,filter: "IN", jobName: \"${this.props.match.params.id}\"  )
    { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`

    if (this.state.filter_end_date !== "") {
        query = ` query { jobruns(limit: ${limit} , offset: ${offset},  startTime: \"${this.state.filter_start_date}\", endTime: \"${this.state.filter_end_date}\",filter: "IN", jobName: \"${this.props.match.params.id}\"  )
        { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
    }

    // if (this.state.filter_end_date !== "" && this.state.filter !== "" ) {
    //   query = ` query { jobruns(limit: ${limit} , offset: ${offset},  startTime: \"${this.state.filter_start_date}\", endTime: \"${this.state.filter_end_date}\",filter: \"${this.state.filter}\", jobName: \"${this.state.job_name}\"  )
    //   { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
    // } else if (this.state.filter_end_date !== "" ){
    //   query = ` query { jobruns(limit: ${limit} , offset: ${offset},  startTime: \"${this.state.filter_start_date}\", endTime: \"${this.state.filter_end_date}\")
    //   { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
    // } else if (this.state.filter !== "") {
    //   query = ` query { jobruns(limit: ${limit} , offset: ${offset}, filter: \"${this.state.filter}\", jobName: \"${this.state.job_name}\"  )
    //   { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
    // } else if (this.state.filter === "" && this.state.job_name !== "") {
    //   query = ` query { jobruns(limit: ${limit} , offset: ${offset} ,filter: "IN", jobName: \"${this.state.job_name}\"  )
    //   { job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
    // } else {
    //   query = ` query { jobruns(limit: ${limit} , offset: ${offset}){ job_run_id job_name  properties state start_time elapsed_time job_type is_master}}`
    // }
    

    this.setState(prevState => ({
      loader: true
    }))

    const response = await fetch(GRAPHQL_URL, {
      method: 'POST',
      headers: {
        'content_type': 'applcation/json',
        'Authorization': token,
      },
      body:  JSON.stringify(
        {
          query: query
        }
      )  
    })

    if (response.status === 403) {
      localStorage.removeItem("Authorization")
    } 

    const {data} = await response.json();
    this.setState(prevState => ({
      loader: false
    }))

    return data;   
  }

  componentDidMount() {
    this.fetchEtlFlowJobs().then(({jobruns}) => {
      this.setState(prevState => ({
        jobruns: jobruns
      }))
    })
 }


 set_job_run_state() {
  this.setState(state => ({
    stepRun: !state.stepRun
  }))
 }
 

 set_job_name() {
  this.setState(prevState => ({
    job_name: document.getElementById("exampleDataList").value
  }))
 }

 set_filter_start_date() {
  this.setState(prevState => ({
    filter_start_date: document.getElementById("start_date").value
  }))
 }

 set_filter_end_date() {
  this.setState(prevState => ({
    filter_end_date: document.getElementById("end_date").value
  }))
 }


 show_modal(data)
 {
   var props = ''
   var data_jobruns = JSON.parse(data)

   for(var key in data_jobruns) {
   { 
       props  += "<b>"+ key + "</b>" +"=" + data_jobruns[key]+ "<br></br>"
   }
  {$("#jobRunModal .modal-body").html(props)}
  $("#jobRunModal").modal();
 }
}

show_job_run_state_modal(data)
 {
  {$("#jobRunStateModal .modal-body").html(data)}
  $("#jobRunStateModal").modal();
 }

 render(){
   
   var button = ''
   var filter_button_name = '' 
   var filter_start_date = ''  
   var filter_end_date = ''  

   //Code for adding pagination element
   if (this.state.paginationValue === 0) {
     button = <li class="page-item disabled">
     <input type="button" value="Previous" class="page-link btn btn-secondary btn-sm"/></li>
   } else {
     button =  <li class="page-item">
     <input type="button" value="Previous" class="page-link btn btn-secondary btn-sm" onClick = {() => {
              this.setState(prevState => ({
               paginationValue: this.state.paginationValue  -   10
             }))
             this.fetchEtlFlowJobs(this.state.paginationValue - 10).then(({jobruns}) => {
               this.setState(prevState => ({
                 jobruns: jobruns
               }))
             }) 
     }}/></li>
   } 


  if (this.state.filter_start_date !== '') {
    filter_start_date = this.state.filter_start_date
  }else {
    filter_start_date = "Start Date"
  } 

  if (this.state.filter_end_date !== '') {
    filter_end_date = this.state.filter_end_date
  }else {
    filter_end_date = "End Date"
  } 

  if(this.state.stepRun) {
     return (
       <div>
         <StepRuns job_run_id={this.job_run_id}  set_job_run_state = {this.set_job_run_state} />
       </div  >
     );
   }else if (this.state.loader){
    return (
      <div>
        <LoaderComponent job_run_id={this.job_run_id}  set_job_run_state = {this.set_job_run_state} />
      </div>
    );
   }
   else{
   return (
     <div className="container-fluid">
       <h3>{this.state.loader}</h3>
       <ul class = "pagination justify-content-center">
         <div class = "form-inline float-right">
              <div class = "mb-1"> 
                  <input type="date"
                         className="form-control input-sm"
                         placeholder={filter_start_date}
                         id = "start_date"
                         onChange={e => this.set_filter_start_date()}
                   ></input>
              </div>
              <div class = "mb-1"> 
                  <input type="date"
                         className="form-control input-sm"
                         placeholder={filter_end_date}
                         id = "end_date"
                         onChange={e => this.set_filter_end_date()}
                   ></input> 
              </div>    
         </div> 
         <button class= "btn btn btn-sm" type="button " onClick={ this.restartState.bind(this) } >
            Clear
         </button>
         <button class= "btn btn btn-sm" type="button " onClick = {() => {
                this.fetchEtlFlowJobs().then(({jobruns}) => {
                     this.setState(prevState => ({
                       jobruns: jobruns
                     }))
                   })
             }} >
            Refresh
         </button>
         <button class= "btn btn btn-sm" type="button " onClick = {() => {
                    this.props.history.push('/')
             }} >
            Back
         </button>

       </ul>
       <table id="jobruns" class="table table-sm table-bordered">
           <thead class="thead-dark">
                 <tr>
                   <th>Job Name</th>
                   <th>Job Type</th>
                   <th>Job Start Time</th>
                   <th>Elapsed Time</th>
                   <th>Job Status</th>
                   <th>Job Properties</th>
                   <th>Step Properties</th>
                 </tr>
               </thead>
                 <tbody>
                 {this.state  .jobruns.map( data => (
                   <tr>
                     <td>{data.job_name}</td>
                     <td>{data.job_type}</td>
                     <td>{data.start_time}</td>
                     <td>{data.elapsed_time}</td>
                     <td>
                     {(() => {
                           if (data.state === "pass" || data.state == "" ) {
                             return (
                               <div>
                                 <p className = "text-success">{data.state}</p>
                               </div>
                             )
                           } else if (data.state === "started" || data.state == "running" ) {
                             return (
                               <div>
                                 <p className = "text-warning">{data.state}</p>
                               </div>
                             )
                           } else {
                            return (
                              <div>
                                 <button type="button" class="btn btn-danger btn btn-sm" data-toggle="modal" onClick = {() => this.show_job_run_state_modal(data.state)}>
                                     Error
                                 </button>
                            
                                 <div class="modal fade" id="jobRunStateModal" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                                   <div class="modal-dialog" role="document">
                                     <div class="modal-content">
                                       <div class="modal-header">
                                         <h5 class="modal-title" id="exampleModalLabel">Properties </h5>
                                         <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                           <span aria-hidden="true">&times;</span>
                                         </button>
                                       </div>
                                       <div class="modal-body">
                                       </div>
                                       <div class="modal-footer">
                                         <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                                       </div>
                                     </div>
                                   </div>
                                 </div>
                              </div>
                            )
                           }
                         })()}
                     </td>
                     <td>
                         <button type="button" class="btn btn-secondary btn-sm" data-toggle="modal" onClick = {() => this.show_modal(data.properties)}>
                           Properties
                         </button>
                         
                         <div class="modal fade" id="jobRunModal" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                           <div class="modal-dialog" role="document">
                             <div class="modal-content">
                               <div class="modal-header">
                                 <h5 class="modal-title" id="exampleModalLabel">Properties </h5>
                                 <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                                   <span aria-hidden="true">&times;</span>
                                 </button>
                               </div>
                               <div class="modal-body">
                               </div>
                               <div class="modal-footer">
                                 <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                               </div>
                             </div>
                           </div>
                         </div>
                     </td>
                     <td><input type="button" value="Step Properties" class="btn btn-secondary btn-sm" onClick = {() => {
                       this.set_job_run_state()
                       this.job_run_id =  data.job_run_id
                     }}/></td>
                   </tr>
                 ))}
                 </tbody>
            </table>  
            <nav aria-label="Page navigation example">
               <ul class="pagination justify-content-center">
                 {button}
                 <li class="page-item"><input type="button" value="Next" class="page-link btn btn-secondary btn-sm" onClick = {() => {
                   this.setState(prevState => ({
                     paginationValue: this.state.paginationValue  + 10
                   }))
                   this.fetchEtlFlowJobs(this.state.paginationValue + 10).then(({jobruns}) => {
                     this.setState(prevState => ({  
                       jobruns: jobruns
                     }))
                   })
                   }}/></li>
               </ul>
         </nav>
          </div>
   );
  }
 }
}
// export default JobRunsByName;     
export default withRouter(JobRunsByName)
